#name {
    font-size: 1rem;
    font-weight: bold;
    /* text-align: center; */
}

#resume.a {
    text-decoration: none; /* Removes the underline typically applied to links */
    font-weight: normal;    /* Sets the font weight to normal (non-bold) */
}

#resume {
    font-family: Arial, Helvetica, sans-serif;
    background-color: rgb(255, 255, 255);
    color: black;
    overflow: auto;
    font-size: 0.75rem;
    line-height: 1.45;
    margin-left: 5%;
    margin-right: 5%;
}

.horizontal-line {
    border-top: 1px solid;
    /* margin-top: 0.5%; */
    margin-bottom: 1.25%;
}

.internal_description {
    /* margin-left: 3%; */
    /* margin-right: 5%; */
    margin-top: 0.5%;
    padding-left: 30px;
    padding-right: 50px;
}

.project_highlight_lower {
    margin-top: 0.5%;
}

#resume a {
    text-decoration: none;
    color: blue;
}
  
#resume a:visited { 
    text-decoration: none;
    color: blue;
}

#resume_header {
    display: flex;
    justify-content: space-between;
}

#contact {
    margin-left: 5%;
    /* margin-right: auto; */
    margin-top: 0.5%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    list-style-type: none;
    /* width: 85%; */
    /* margin-right: 10%; */
}

#contact_info {
    margin-top: 0.3%;
}

.contact_item {
    margin-right: 5%;
}

#contact p {
}

.prefix_img {
    width: 15px;
    margin-left: 7%;
}

#resume #basic_info p {
    margin-top: 0.6%;
}

#education {
    display: flex;
    margin-left: 0.5%;
    margin-top: 1%;
    margin-bottom: 1%;
}

#ms {
    margin-top: 1.5%;
}

#bs {
    margin-top: -0.75%;
}

#education div {
    margin-top: 1.75%;
}

#cap {
    min-width: 3%;
    max-width: 3%;
    margin-top: 0.75%;
    /* aspect-ratio: 0.76; */
    margin-left: 1%;
    margin-right: 2%;
}

#skills table {
    font-weight: normal;
    border: 0.5px solid;
    border-color: rgba(0, 0, 0, 0.2);
    border-collapse: collapse;
}

#skills table td, #skills table th {
    font-weight: normal;
    border: 0.5px solid;
    border-color: rgba(0, 0, 0, 0.2);
    border-collapse: collapse;
    background-color: rgba(0, 0, 0, 0.1);
    width: 20%;
    padding: 8px;
    vertical-align: top;
}

#skills table th {
    background-color: rgba(0, 0, 0, 0);
    text-align: left;
}

.block {
    margin-top: 1.5%;
}

#visa {
    margin-top: 2.5%;
    margin-bottom: -1%;
}

.block p {
}

.block ul {
    width: 90%;
    margin-top: 0.75%;
    margin-left: 3%;
}

.block ul li {
    margin-top: 0.5%;
}

.inner_block {
    margin-bottom: 1%;
}

#summary{
    margin-top: 2%;
}

#summary .inner_block, .project_highlight, .award_highlight {
    /* margin-left: 1%; */
    /* margin-right: 1%; */
}

.project_highlight, .award_highlight {
    width: 100%;
}

.more_left {
    margin-top: 1.25%;
    margin-left: 3.25%;
}

.block_item {
    display: flex;
    justify-content: space-between;
}

.duration {
    text-align: end;
}

.location {
    margin-top: -0.75%;
    text-align: end;
}

.highlight {
    display: inline-block;
}

.project_highlight {
    display: inline-block;
}

.block_category {
    font-weight: bold;
}

.highlight_inner {
    font-size: 0.7rem;
}

.first_item {
    margin-top: -0.25%;
}

#experience .inner_block .block_item .highlight_inner {
    margin-top: 0.1%;
}

.bold {
    font-weight: bold;
}

.company {
    display: inline-block;
    font-weight: bold;
}

.after_highlight {
    display: inline-block;
}

.blank {
    font-size: 0.1rem;
    color: white;
}